<template>
    <div class="role-box">
        <div class="query">
            <span class="title">角色名称</span>
            <input type="text" v-model="roleName" style='font-size:18px'>
            <span class="query-btn" @click="getRoleData()">查询</span>
        </div>  
        <div class="add-role">
            <span class="add-role-btn"><router-link tag="span" :to="{name:'roleManager',params:{ key:'add'}}">新增角色</router-link></span>
        </div>
        <div class="role-table">
            <div>
                <el-table
                    :data="roleTableData"
                    style="width: 100%">
                <el-table-column
                    prop="roleName"
                    label="角色名称"
                    align="center"
                    >
                </el-table-column>
                <el-table-column
                    prop="createTime"
                    label="创建时间"
                    align="center"
                    >
                </el-table-column>
                <el-table-column
                    align="center"
                    label="标准价格（元/年）">
                    <template slot-scope="scope">
                        <span v-if="scope.row.standardPrice">{{scope.row.standardPrice}}</span>
                        <span v-else>——</span>
                    </template>
                </el-table-column>
                <el-table-column
                    align="center"
                    label="续费价格（元/年）">
                    <template slot-scope="scope">
                        <span v-if="scope.row.renewPrice">{{scope.row.renewPrice}}</span>
                        <span v-else>——</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="webUserCount"
                    align="center"
                    width="100"
                    label="使用人数">
                </el-table-column>
                <el-table-column
                    label="操作"
                    align="center"
                    prop="state">
                    <template slot-scope="scope">
                        <!-- <el-button @click="handleClickModify(scope.row)" type="text" size="small" style="cursor: pointer;">修改</el-button> -->
                        <el-button @click="handleClickSee(scope.row)" type="text" size="small">查看</el-button>
                        <el-button v-if="scope.row.state == '0' && scope.row.webUserCount == '0'" @click="handleClickModify(scope.row)" type="text" size="small" style="cursor: pointer;">修改</el-button>
                        <el-button v-else type="text" size="small" style="cursor: pointer;">不可修改</el-button>
                        <el-button v-if="scope.row.state == '0' && scope.row.webUserCount == '0'" @click="handleClickDel(scope.row)" type="text" size="small">删除</el-button>
                        <el-button v-else type="text" size="small">不可以删除</el-button>
                    </template>
                </el-table-column>
                 
            </el-table>
            </div>
            <div class="paging-box">
                <div></div>
                <div>
                    <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="pageSize"
                        layout="total,prev, pager, next, jumper"
                        :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Axios from 'axios'
import { javaServiceUrl } from './../../util/constant.js'

export default {
    data(){
        return{
            roleName:'',//input输入框
            roleTableData:[], //列表数据
            currentPage:1,//当前页
            pageSize:10,
            total:0,
            pageNum:1,//第几页
            state:'',//state等于0时 才可以删除
            authorization:"",//请求头
            token:"",
        }
    },
    created(){
        var storage = window.localStorage;
        this.authorization = storage.getItem("userId");
        this.token = storage.getItem("token");

    },
    mounted(){
        this.getRoleData();
    },
    methods:{
        //查看
        handleClickSee(scope){
            this.$router.push({
                name: 'roleManager',
                params: {
                    row:scope,
                    key:"look"

                }
            })
        },
        //修改
        handleClickModify(scope,row){
             this.$router.push({
                name: 'roleManager',
                params: {
                    row:scope,
                    key:"modify"

                }
            })
        },
        //删除
        handleClickDel(scope,row){
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                Axios({
                    url:javaServiceUrl + '/userPermission/deleteRoles',
                    method: 'get',
                    headers:{"Authorization":"user-" + this.authorization + "-" +this.token}, 
                    params:{
                        roleIds:scope.id
                    }
                })
                .then(res=>{
                    if(res.data.msg){
                        this.$router.push({
                            name:'login',
                        })
                    }else{
                        this.getRoleData();
                    }
                })
                .catch(err=>{
                    console.log(err)
                })
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
         
        },
        handleSizeChange(val) {

        },
        handleCurrentChange(val) {
            this.pageNum = val;
            this.getRoleData();
        },
        //请求列表
        getRoleData(){
            Axios({
                url: javaServiceUrl + '/userPermission/findRoles',
                method: 'get',
                headers: {"Authorization":"user-" + this.authorization + "-" +this.token},    
                params:{
                    roleName:this.roleName,
                    pageSize:this.pageSize,
                    pageNum:this.pageNum - 1
                }
            })
            .then(res=>{
                if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                    this.roleTableData = res.data.list;
                    this.total = res.data.count;
                    for(var i = 0;i < res.data.list.length; i++){
                        this.state = res.data.list[i].state;
                        this.roleTableData[i].createTime = this.roleTableData[i].createTime.substring(0,10);
                    }
                }
              
            })
            .catch(err=>{
                console.log(err)
            })
        },

        //请求列表
        deleterRole(){
            
        }
    }
}
function getNowFormatDate(e) {
    var date = new Date(e);
    var seperator1 = "-";
    var seperator2 = ":";
    var month = date.getMonth() + 1;
    var strDate = date.getDate();
    if(month >= 1 && month <= 9) {
        month = "0" + month;
    }
    if(strDate >= 0 && strDate <= 9) {
        strDate = "0" + strDate;
    }
    var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
    //  +" " + date.getHours() + seperator2 + date.getMinutes() +
    //     seperator2 + date.getSeconds();
    return currentdate;
}
</script>
<style lang="scss" scoped>
.role-box{
    .query{
        line-height: 80px;
        background: #fff;
        border-radius: 8px;
        .title{
            margin-left: 20px;
        }
        input{
            width: 196px;
            height: 40px;
            border: 1px solid #f2f2f2;
            border-radius: 8px;
            margin: 0 10px;
        }
        .query-btn{
            display: inline-block;
            width: 76px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            background: rgb(96,152,226);
            color: #fff;
            cursor: pointer;
            border-radius: 15px;
        }
    }
    .add-role{
        height: 60px; 
        line-height: 60px;
        display: flex;
        justify-content: flex-end;
        margin-right: 20px;
        .add-role-btn{
            display: inline-block;
            width: 89px;
            height: 30px;
            margin-top: 15px;;
            line-height: 30px;
            text-align: center;
            border-radius: 15px;
            background: rgb(241,145,73);
            color: #fff;
            font-size: 14px;
        }
    }
    .paging-box{
        display: flex;
        justify-content: space-between;
        margin-top: 15px;
    }
}
</style>



